<template>
  <div :class="data_type">
    <l-geo-json
      :geojson="geojson_covid_vaccine_json"
      :options="options"
      :options-style="styleFunction"
    />
  </div>
</template>

<script>
import { LGeoJson } from "vue2-leaflet";

export default {
  name: "geo-json-layer",
  components: {
    LGeoJson,
  },

  props: ["geojson_covid_vaccine_json", "data_type"],

  computed: {
    options: {
      get() {
        console.log(this.data_type);
        return {
          onEachFeature: (feature, layer) => {
            layer.options.fillColor = this.getColor(
              this.data_type,
              feature.properties.covid_data[this.data_type]
            );
            feature.properties.covid_data
              ? layer.on("click", (e) =>
                  this.$emit(
                    "handleSelectedCountry",
                    e.target.feature.properties.covid_data
                  )
                )
              : null;
          },
        };
      },
    },
    styleFunction: {
      get() {
        return {
          weight: 2,
          color: "rgba(0, 0, 0, 0.2)",
          opacity: 1,

          fillOpacity: 1,
        };
      },
    },
  },

  methods: {
    getColor(type, value) {
      return type === "people_vaccinated_per_hundred"
        ? (() =>
            value * 10000 > 1500
              ? "#008000"
              : value * 10000 > 1250
              ? "#00b300"
              : value * 10000 > 1000
              ? "#00e600"
              : value * 10000 > 750
              ? "#1aff1a"
              : value * 10000 > 500
              ? "#4dff4d"
              : value * 10000 > 250
              ? "#80ff80"
              : value * 10000 > 100
              ? "#b3ffb3"
              : "#e6ffe6")()
        : type === "total_cases_per_million"
        ? (() =>
            value > 1500
              ? "#997300"
              : value > 1250
              ? "#cc9900"
              : value > 1000
              ? "#e6ac00"
              : value > 750
              ? "#ffc61a"
              : value > 500
              ? "#ffd24d"
              : value > 250
              ? "#ffdf80"
              : value > 100
              ? "#ffecb3"
              : "#fff9e6")()
        : (() =>
            value > 1500
              ? "#800000"
              : value > 1250
              ? "#b30000"
              : value > 1000
              ? "#e60000"
              : value > 750
              ? "#ff1a1a"
              : value > 500
              ? "#ff4d4d"
              : value > 250
              ? "#ff8080"
              : value > 100
              ? "#ffb3b3"
              : "#ffe6e6")();
    },
  },
};
</script>